import React from 'react';
import './FaceBox.css'

const FaceBox = ({imageUrl, boxes}) =>{
    return (
        <div className="center ma">
           <div className="absolute mt2">
           <img 
           src={imageUrl} 
           alt="" 
           id='inputImage'
           width='500px'
           height="auto"
           />

           {boxes.map((box)=>{
               return <div key={box.leftCol} className="bounding-box"
               style={{top: box.topRow, right: box.rightCol, bottom: box.bottomRow, left: box.leftCol}}
               ></div>
           })}
           </div>
        </div>
    )
}

export default FaceBox;