import React from "react";
import "./App.css";
import Particles from "react-particles-js";
import Clarifai from "clarifai";

import Navigation from "./components/Navigation/Navigation";
import Logo from "./components/Logo/Logo";
import ImageInput from "./components/ImageInput/ImageInput";
import FaceBox from "./components/FaceBox/FaceBox";

const app = new Clarifai.App({
  apiKey: "e0cc428e4e134145a7c367b562da6bb3",
});

const particlesConfing = {
  particles: {
    number: {
      value: 150,
      density: {
        enable: true,
        value_area: 800,
      },
    },
  },
};
class App extends React.Component {
  constructor() {
    super();
    this.state = {
      input: "",
      imageUrl: "",
      boxes: [],
    };
  }
  onSubmit = () => {
    this.setState({ imageUrl: this.state.input });
    app.models
      .predict(Clarifai.FACE_DETECT_MODEL, this.state.input)
      .then((response) => {
        this.displayBox(this.getFaceLocation(response));
      })
      .catch((err) => console.log(err));
  };
  onInput = (event) => {
    this.setState({ input: event.target.value });
  };
  getFaceLocation = (data) => {
    const image = document.getElementById('inputImage');
    const width = Number(image.width);
    const height = Number(image.height);

    return data.outputs[0].data.regions.map((face) => {
      console.log(face.region_info);
      const faceLocation = face.region_info.bounding_box;
      return {
        topRow: faceLocation.top_row * height,
        leftCol: faceLocation.left_col * width,
        bottomRow: height - (faceLocation.bottom_row * height),
        rightCol: width - (faceLocation.right_col * width),
      }
    })
  };

  displayBox = (boxes) =>{
    this.setState({boxes: boxes})
  }
  render() {
    return (
      <div className="App">
        <Particles className="particles" params={particlesConfing} />
        <Navigation />
        <Logo />
        <ImageInput onInput={this.onInput} onSubmit={this.onSubmit} />
        <FaceBox boxes={this.state.boxes} imageUrl={this.state.imageUrl} />
      </div>
    );
  }
}

export default App;
