import React from 'react';
import './Logo.css'
import Tilt from 'react-tilt'
import logo from './logo.png'

const Logo = () => {
    return(
        <div className="ma4 mt0">
            <Tilt 
            className="Tilt br3 shadow-2 pointer" 
            options={{ max : 55 }} 
            style={{ 
                height: 150, 
                width: 150 }} >
            <div className="Tilt-inner pa3 flex justify-center"> 
            <img src={logo} 
            alt="logo" 
            style={{paddingTop: '5px'}}/>
            </div>
            </Tilt>
        </div>
    )
}

export default Logo;