import React from 'react';
import './ImageInput.css';

const ImageInput = ({onInput, onSubmit}) =>{
    return (
        <div>
            <p className="f3">
                {'This APP will detect faces in Your pictures. Give it a try'}
            </p>
            <div className="center ma3">
                <div className="form center pa4 br shadow-5">
                <input type="text" 
                className="f4 pa2 w-70 center"
                onChange={onInput}
                />
                <button className="w-30 grow f4 link ph3 pv2 dib white bg-light-purple pointer" onClick={onSubmit}> Detect </button>
                </div>
            </div>
        </div>
    )
}

export default ImageInput;